import "./Form.sass";
import { useForm } from "react-hook-form";
import {
  nameRegExp,
  descriptionRegExp,
  emailRegExp,
  phoneRegExp,
} from "../../utils/regExp";
import { sendEmail } from "../../utils/api/emailJSApi";
import Captcha from "../Capcha/Captcha";
import {
  YOUR_PUBLIC_KEY,
  REGISTRATION_TEMPLATE_ID,
  YOUR_SERVICE_ID,
} from "../../utils/emailJS";
import { useState } from "react";

const Form = ({ itemForRegistration, setDataForResponsePopup }) => {
  const [isBtnActive, setIsBtnActive] = useState(false);
  const [isCaptchaOk, setIsCaptchaOk] = useState(false);

  const {
    register,
    handleSubmit,
    formState: { errors, isValid },
  } = useForm({
    mode: "all",
  });
  const { name } = itemForRegistration;

  //          subtitle
  const changeEnding = () => {
    if (itemForRegistration.price) {
      return "";
    } else {
      return "";
    }
  };

  const subtitle = changeEnding();

  //   
  const onSubmit = (data) => {
    //  ,          ( ,    )
    const registrationData = {
      ...data,
      ...itemForRegistration,
    };

    //           
    setIsBtnActive(true);

    //             emailJS
    const dataToSend = {
      service_id: YOUR_SERVICE_ID,
      template_id: REGISTRATION_TEMPLATE_ID,
      user_id: YOUR_PUBLIC_KEY,
      template_params: {
        name: `${registrationData.firstName} ${registrationData.secondName}`,
        phone: registrationData.phoneNumber,
        email: registrationData.email,
        comment: registrationData.comments,
        agree: "",
        event: registrationData.name,
        address: registrationData.address,
        date: registrationData.date,
      },
    };

    //    
    sendEmail(dataToSend)
      .then(() => {
        setIsBtnActive(false);
        setDataForResponsePopup({
          isOpened: true,
          isSaccess: true,
        });
      })
      .catch((err) => {
        setDataForResponsePopup({
          isOpened: true,
          isSaccess: false,
        });
        console.log(err);
      });
  };

  return (
    <form className="form" onSubmit={handleSubmit(onSubmit)}>
      <h2 className="form__title">{name}</h2>
      <p className="form__subtitle">{`  ${subtitle}`}</p>
      <p className="form__required-field">
        <span className="form__asterisk">&#x2a;</span> -  
         
      </p>
      <div className="form__inputs-block">
        <label className="form__input-lable">
          <span>
             <span className="form__asterisk">&#x2a;</span>
          </span>
          <input
            className="form__input-field"
            placeholder=""
            type="text"
            name="firstName"
            {...register("firstName", {
              required: " ",
              maxLength: {
                value: 30,
                message: "  30 ",
              },
              minLength: {
                value: 2,
                message: "   2 ",
              },
              pattern: {
                value: nameRegExp,
                message: "  ",
              },
            })}
            aria-invalid={errors.firstName ? "true" : "false"}
          />
          {errors.firstName && (
            <p role="alert" className="form__error-text">
              {errors.firstName?.message}
            </p>
          )}
        </label>

        <label className="form__input-lable">
          <span>
             <span className="form__asterisk">&#x2a;</span>
          </span>
          <input
            className="form__input-field"
            placeholder=""
            type="text"
            name="secondName"
            {...register("secondName", {
              required: " ",
              maxLength: {
                value: 30,
                message: "  30 ",
              },
              minLength: {
                value: 2,
                message: "   2 ",
              },
              pattern: {
                value: nameRegExp,
                message: "  ",
              },
            })}
            aria-invalid={errors.secondName ? "true" : "false"}
          />
          {errors.secondName && (
            <p role="alert" className="form__error-text">
              {errors.secondName?.message}
            </p>
          )}
        </label>

        <label className="form__input-lable">
          <span>
             <span className="form__asterisk">&#x2a;</span>
          </span>
          <input
            className="form__input-field"
            placeholder="+7 () --"
            type="number"
            name="phoneNumber"
            {...register("phoneNumber", {
              required: " ",
              maxLength: {
                value: 12,
                message: "  12 ",
              },
              minLength: {
                value: 10,
                message: "   10 ",
              },
              pattern: {
                value: phoneRegExp,
                message: "   ",
              },
            })}
          />
          {errors.phoneNumber && (
            <p role="alert" className="form__error-text">
              {errors.phoneNumber?.message}
            </p>
          )}
        </label>

        <label className="form__input-lable">
           
          <input
            className="form__input-field"
            placeholder="example@mail.ru"
            type="email"
            name="email"
            {...register("email", {
              required: false,
              maxLength: {
                value: 50,
                message: "  50 ",
              },
              pattern: {
                value: emailRegExp,
                message: "    ",
              },
            })}
          />
          {errors.email && (
            <p role="alert" className="form__error-text">
              {errors.email?.message}
            </p>
          )}
        </label>

        <label className="form__input-lable">
          
          <input
            className="form__input-field"
            placeholder=" ..."
            type="text"
            name="comments"
            {...register("comments", {
              required: false,
              maxLength: {
                value: 200,
                message: "  200 ",
              },
              pattern: {
                value: descriptionRegExp,
                message: "     ",
              },
            })}
          />
          {errors.comments && (
            <p role="alert" className="form__error-text">
              {errors.comments?.message}
            </p>
          )}
        </label>
      </div>

      <Captcha setIsCaptchaOk={setIsCaptchaOk} isCaptchaOk={isCaptchaOk} />

      <div className="form__agreement">
        <input
          className="form__agreement-checkbox"
          type="checkbox"
          name="form-checkbox"
          id="form-checkbox"
          required
          {...register("checked", {
            required: " ",
          })}
        />
        <label htmlFor="form-checkbox" className="form__agreement-lable">
           ,   {" "}
          <a className="form__agreement-link" href="/agreement">
             
          </a>{" "}
                  , 
               27.07.2006  152- 
           .
        </label>
      </div>

      <button
        type="submit"
        className={`form__button ${
          (!isValid || !isCaptchaOk) && "form__button_disabled"
        } ${isBtnActive && isCaptchaOk && "form__button_active"} `}
        disabled={(!isValid || !isCaptchaOk) && "disabled"}
      >
        
      </button>
    </form>
  );
};

export default Form;
