import "./PayForm.sass";
import { useForm } from 'react-hook-form';
import Button from "../Button/Button";
import useMediaQuery from "../../utils/hooks/useMediaQuery";

const PayForm = () => {
    const {
        register,
        // handleSubmit,
        formState: { errors, /*isValid*/ },
      } = useForm({
        mode: "all",
      });

    const isDesktop = useMediaQuery("(min-width: 767px)");

      // const onSubmit = (data) => {
      //   alert(JSON.stringify(data));
      // };

    return (
        <div className="payment__wrapper">
            <div className="payment__card transfer-wrapper">
                <form className="payment__money-transfer">
                    <h3 className="payment__title"> </h3>
                    <ul className="payment__input_list">
                        <li className="payment__input_item">
                            <label className="payment__input-lable"></label>
                            <input className="payment__input-field"
                                placeholder=""
                                type="text"
                                name="firstName"
                                {...register("firstName", {
                                required: " ",
                                maxLength: {
                                    value: 30,
                                    message: "  30 ",
                                },
                                pattern: {
                                    value: /^([-\s]+|[a-z\s]+)$/iu,
                                    message: "     ",
                                },
                                })}
                                aria-invalid={errors.firstName ? "true" : "false"}
                            />
                            {errors.firstName && (
                                <p role="alert" className="payment__input__error-text">
                                {errors.firstName?.message}
                                </p>
                            )}
                        </li>
                        <li className="payment__input_item">
                            <label className="payment__input-lable"></label>
                            <input className="payment__input-field"
                                placeholder=""
                                type="text"
                                name="lastName"
                                {...register("lastName", {
                                required: " ",
                                maxLength: {
                                    value: 30,
                                    message: "  30 ",
                                },
                                pattern: {
                                    value: /^([-\s]+|[a-z\s]+)$/iu,
                                    message: "     ",
                                },
                                })}
                                aria-invalid={errors.lastName ? "true" : "false"}
                            />
                            {errors.lastName && (
                                <p role="alert" className="payment__input__error-text">
                                {errors.lastName?.message}
                                </p>
                            )}
                        </li>
                        <li className="payment__input_item">
                            <label className="payment__input-lable"></label>
                            <input className="payment__input-field"
                                placeholder=""
                                type="tel"
                                name="phoneNumber"
                                {...register("phoneNumber", {
                                required: " ",
                                maxLength: {
                                    value: 12,
                                    message: "  12 ",
                                },
                                pattern: {
                                    value: /^((8|\+7)[- ]?)?(\(?\d{3}\)?[- ]?)?[\d\- ]{7,10}$/,
                                    message: "   ",
                                },
                                })}
                                aria-invalid={errors.phoneNumber ? "true" : "false"}
                            />
                            {errors.phoneNumber && (
                                <p role="alert" className="payment__input__error-text">
                                {errors.phoneNumber?.message}
                                </p>
                            )}
                        </li>
                        <li className="payment__input_item">
                            <label className="payment__input-lable"> </label>
                            <input className="payment__input-field"
                                placeholder=""
                                type="email"
                                name="email"
                                {...register("email", {
                                required: " ",
                                maxLength: {
                                    value: 50,
                                    message: "  50 ",
                                },
                                pattern: {
                                    value: /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/iu,
                                    message: "    ",
                                },
                                })}
                                aria-invalid={errors.email ? "true" : "false"}
                            />
                            {errors.email && (
                                <p role="alert" className="payment__input__error-text">
                                {errors.email?.message}
                                </p>
                            )}
                        </li>
                        <li className="payment__input_item">
                            <label className="payment__input-lable"></label>
                            <input className="payment__input-field"
                                placeholder=""
                                type="number"
                                name="summ"
                                {...register("summ", {
                                required: " ",
                                maxLength: {
                                    value: 10,
                                    message: "  10 ",
                                },
                                pattern: {
                                    value: /\d/,
                                    message: "   ",
                                },
                                })}
                                aria-invalid={errors.summ ? "true" : "false"}
                            />
                            {errors.summ && (
                                <p role="alert" className="payment__input__error-text">
                                {errors.summ?.message}
                                </p>
                            )}
                        </li>
                        <li className="payment__input_item">
                        <label className="payment__input-lable"> </label>
                            <select className="payment__input-field payment__select" name="payment" id="payment-type">
                                <option value="choice" selected></option>
                                <option value="choice">QIWI</option>
                                <option value="choice">BTC</option>
                            </select>
                        </li>
                    </ul>
                    <div className="payment__checkbox-wrapper">
                        <label className="check-label one-check" htmlFor="one-time">
                            <input className="check" type="checkbox" id="one-time" />
                            <span className="check-decor-span"></span>
                        </label>
                        <label className="check-label" htmlFor="one-month">
                            <input className="check" type="checkbox" id="one-month" />
                            <span className="check-decor-span"></span>
                        </label>
                    </div>
                    <div className="payment__comment-wrapper">
                        <label className="payment__label"  htmlFor="payment-comment"></label>
                        <textarea className="payment__comment" name="comment" id="payment-comment"
                            placeholder=" ...">
                        </textarea>
                    </div>
                    {isDesktop ? (<><Button title=" " width="281px" route="/help" />{" "}</>) : (<>{" "}<Button title=" " width="225px" route="/help" />{" "}</>)}
                </form>
            </div>
            <div className="payment__card card-pay-wrapper">
                <form className="payment__card-pay">
                    <div>
                    <h3 className="payment__title"> </h3>
                    <ul className="payment__input_list">
                        <li className="payment__input_item long-input">
                            <label className="payment__input-lable"> </label>
                            <input className="payment__input-field long-input"
                                placeholder=""
                                type="text"
                                name="cardName"
                                {...register("cardName", {
                                required: " ",
                                maxLength: {
                                    value: 50,
                                    message: "  50 ",
                                },
                                pattern: {
                                    value: /^([a-z\s]+)$/iu,
                                    message: "   ",
                                },
                                })}
                                aria-invalid={errors.cardName ? "true" : "false"}
                            />
                            {errors.cardName && (
                                <p role="alert" className="payment__input__error-text">
                                {errors.cardName?.message}
                                </p>
                            )}
                        </li>
                        <li className="payment__input_item long-input">
                            <label className="payment__input-lable"> </label>
                            <input className="payment__input-field long-input"
                                placeholder=""
                                type="number"
                                name="cardNumber"
                                {...register("cardNumber", {
                                required: " ",
                                maxLength: {
                                    value: 16,
                                    message: "16 ",
                                },
                                pattern: {
                                    value: /\d/,
                                    message: "  ",
                                },
                                })}
                                aria-invalid={errors.cardNumber ? "true" : "false"}
                            />
                            {errors.cardNumber && (
                                <p role="alert" className="payment__input__error-text">
                                {errors.cardNumber?.message}
                                </p>
                            )}
                        </li>
                        <li className="payment__input_item validity-input">
                            <label className="payment__input-lable"> </label>
                            <input className="payment__input-field validity-input"
                                placeholder=""
                                type="text"
                                name="validity"
                                {...register("validity", {
                                required: " ",
                                maxLength: {
                                    value: 5,
                                    message: "xx/xx",
                                },
                                pattern: {
                                    value: /\d\d[/]\d\d/,
                                    message: "   'xx/xx'",
                                },
                                })}
                                aria-invalid={errors.validity ? "true" : "false"}
                            />
                            {errors.validity && (
                                <p role="alert" className="payment__input__error-text">
                                {errors.validity?.message}
                                </p>
                            )}
                        </li>
                        <li className="payment__input_item CVC-input">
                            <label className="payment__input-lable">CVC-</label>
                            <input className="payment__input-field CVC-input"
                                placeholder=""
                                type="text"
                                name="CVC"
                                {...register("CVC", {
                                required: " ",
                                maxLength: {
                                    value: 3,
                                    message: "xxx",
                                },
                                pattern: {
                                    value: /\d\d\d/,
                                    message: "   'xxx'",
                                },
                                })}
                                aria-invalid={errors.CVC ? "true" : "false"}
                            />
                            {errors.CVC && (
                                <p role="alert" className="payment__input__error-text">
                                {errors.CVC?.message}
                                </p>
                            )}
                        </li>
                    </ul>
                    </div>
                    {isDesktop ? (<><Button title="  " width="281px" route="/help" />{" "}</>) : (<>{" "}<Button title="  " width="225px" route="/help" />{" "}</>)}
                </form>
            </div>
        </div>
    )
}
export default PayForm;