export const nameRegExp = /^[a-z--]+$/iu;

//   ,      
export const fullNameRegExp = /^[a-z- ,.'-]+$/iu;

// export const descriptionRegExp = /^[a-z0-9-; .,-]+$/iu;

export const descriptionRegExp = /^[a-z0-9-;. ,-][a-z0-9-; .,-]+$/iu;

export const emailRegExp = /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/iu;

export const phoneRegExp = /^((8|\+7)[- ]?)?(\(?\d{3}\)?[- ]?)?[\d\- ]{7,10}$/;